<template>
  <div>
    <h1></h1>
    <el-table :data="items">
      <el-table-column prop="_id" label="ID"></el-table-column>
      <el-table-column prop="username" label=""></el-table-column>
      <el-table-column fixed="right" label="" width="100">
        <template slot-scope="scope">
          <!-- <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button> -->
          <el-button
            type="text"
            @click="$router.push(`/admin_users/edit/${scope.row._id}`)"
            size="small"
          ></el-button>
          <el-button
            type="text"
            @click="remove(scope.row)"
            size="small"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      items: []
    };
  },
  methods: {
    getCategoryList() {
      this.$http.get("rest/admin_users").then(res => {
        this.items = res.data;
      });
    },
    remove(row) {
      this.$confirm(`"${row.username}"`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$http.delete(`rest/admin_users/${row._id}`).then(res => {
            console.log(res)
            if(res.data.success){
              this.getCategoryList()
              this.$message({
                type: 'success',
                message: '!'
              });
            }
          })
        })
        .catch(() => {

        })
    }
  },
  created() {
    this.getCategoryList();
  }
};
</script>