<template>
  <div class="about">
    <h1>{{id ? '': ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <!-- <el-form-item label="">
        <el-select v-model="model.parent">
          <el-option v-for="item in parents" :key="item._id"
          :label="item.name" :value="item._id"></el-option>
        </el-select>
      </el-form-item>-->
      <el-form-item label="">
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-upload
          class="avatar-uploader"
          :action="uploadUrl"
          :headers="getAuthHeaders()"
          :show-file-list="false"
          :on-success="afterUpload"
        >
          <img v-if="model.image" :src="model.image" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="model.description"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      model: {}
    };
  },
  methods: {
    afterUpload(res) {
      this.$set(this.model, 'image', res.url)
    },
    async save() {
      let res;
      if (this.id) {
        res = await this.$http.put(`rest/strategies/${this.id}`, this.model);
      } else {
        res = await this.$http.post("rest/strategies", this.model);
      }
      console.log(res);
      this.$router.push("/strategies/list");
      this.$message({
        type: "success",
        message: ""
      });
    },
    getStrategies() {
      this.$http.get(`rest/strategies/${this.id}`).then(res => {
        this.model = res.data;

      });
    }
  },
  created() {
    this.id && this.getStrategies();
  },
  watch: {
    $route: function() {
      this.model = {};
    }
  }
};
</script>

