<template>
  <div class="about">
    <h1>{{id ? '': ''}}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-button size="small" @click="model.items.push({})">
          <i style="margin-right:0.2rem;" class="el-icon-plus"></i>
        </el-button>
        <el-row type="flex" style="flex-wrap: wrap">
          <el-col :md="24" v-for="(item, index) in model.items" :key="index">
            <el-form-item label="(URL)">
              <el-input v-model="item.url"></el-input>
            </el-form-item>
            <el-form-item label="" style="margin-top:0.5rem">
              <el-upload
                class="avatar-uploader"
                :action="uploadUrl"
                :headers="getAuthHeaders()"
                :show-file-list="false"
                :on-success="res => $set(item, 'image', res.url)"
              >
                <img v-if="item.image" :src="item.image" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item>
              <el-button size="small" type="danger" @click="model.items.splice(index, 1)"></el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      model: {
        items: []
      }
    };
  },
  methods: {
    async save() {
      let res;
      if (this.id) {
        res = await this.$http.put(`rest/ads/${this.id}`, this.model);
      } else {
        res = await this.$http.post("rest/ads", this.model);
      }
      console.log(res);
      this.$router.push("/ads/list");
      this.$message({
        type: "success",
        message: ""
      });
    },
    getAds() {
      this.$http.get(`rest/ads/${this.id}`).then(res => {
        this.model = Object.assign({}, this.model, res.data);
      });
    }
  },
  created() {
    this.id && this.getAds();
  },
  watch: {
    $route: function() {
      this.model = {};
    }
  }
};
</script>